<template>
  <div class="ShoppingCart_wrapper">

    <div class="interval">
      <div class="rectangle"></div>
      <p>購物車</p>
    </div>

    <table class="titleName" style="margin: 0 auto;">
      <tr style="background-color: rgb(87, 87, 87, .5); color: rgb(255, 255, 255);">
        <td>商品印象</td>
        <td>商品名稱</td>
        <td>商品價格</td>
        <td>商品數量</td>
        <td>編輯商品</td>
      </tr>
    </table>

    <div class="item_content">
      <table>
        <tr class="item" v-for="item in order_list_local" :key='item'>
          <td><img :src="item.src"></td>
          <td>{{item.title}}</td>
          <td>NT$ {{item.price * item.quantity}}</td>
          <td>{{item.quantity}}</td>
          <td><button @click="deleteItem">刪除商品</button></td>
        </tr>
      </table>
    </div>

    <table class="titleName" style="margin: 0 auto;">
      <tr style="background-color: rgb(87, 87, 87, .5); color: rgb(255, 255, 255);">
        <td>商品總價格: <span style="color: red;">NT$ {{total_price}}</span></td>
      </tr>
    </table>

    <footer>
      <div>
        <img src="../assets/旅人logo_2.png">
        <p>
          © 2022 TRAVELER 旅人<br>
          訂房專線：0912-345-678<br>
          地址：263新北市永和區新南路一段58號
        </p>
      </div>
    </footer>

  </div>
</template>

<script>
export default {
    name: "ShoppingCart",
    components: {},
    data: () => ({
      order_list_local:[],
      total_price:[]
    }),
    methods: {
      deleteItem(e){
        let itemId = e.target.parentNode.parentNode
        console.log(itemId);
        this.order_list_local.splice(itemId,1);
        localStorage.setItem('shopCarItem', JSON.stringify(this.order_list_local));

        // 清除 storage
        // storage['shopCarItem'] = storage['shopCarItem'].replace(`${e.target}, `,``)
        // storage.removeItem(itemId)
      }
    },
    mounted(){
      this.order_list_local = JSON.parse(localStorage.getItem('shopCarItem'));
      console.log(this.order_list_local);
    },
    updated(){
      let total = 0;
      for(let i=0;i<this.order_list_local.length;i++){
        total += (this.order_list_local[i]['quantity'] * this.order_list_local[i]['price'])
      }
      this.total_price = total;
    }
};
</script>

<style lang="scss">
.ShoppingCart_wrapper{
  table{
    width: 80%;
    tr{
      td{
        border: 1px solid rgb(87, 87, 87, .5);
        text-align: center;
        padding: 5px;
        img{
            width: 200px;
          }
        button{
          background-color: rgb(87, 87, 87, .5);
          color: rgb(255, 255, 255); 
          padding: 5px;
        }
      }
      td:nth-child(1){ width: 35%; }
      td:nth-child(2){ width: 25%; }
      td:nth-child(3){ width: 15%; }
      td:nth-child(4){ width: 10%; }
      td:nth-child(5){ width: 15%; }
    }
  }
  .item_content{
    display: flex;
    justify-content: center;
  }
  .titleName{
    font-size: 18px;
  }
}

</style>